import React from 'react';

class Login extends React.Component{

    constructor(props){
        super(props);
        this.state = {emailaddress:'',
                      password:'' 
                        }

    }
    validatelogin =(event)=>{
        event.preventDefault()

        if (this.state.emailaddress==='admin' && this.state.password==='admin') {

            localStorage.setItem('token',123)
            this.props.validatelogin()
        }
        
        
    }
    

render(){
    return (<div>
            <form className="form-signin">
                <img className="mb-4" src="/docs/4.4/assets/brand/bootstrap-solid.svg" alt="" width="72" height="72" />
                <h1 className="h3 mb-3 font-weight-normal">Please sign in</h1>
                
                <input type="email" id="inputEmail" className="form-control" placeholder="Email address" required="" autoFocus=""  value = {this.state.emailaddress}  onChange={(item) => { this.setState({ emailaddress: item.target.value }) }}/>
                <br/> <br/>
                <input type="password" id="inputPassword" className="form-control" placeholder="Password" required="" value = {this.state.password}  onChange={(item) => { this.setState({ password: item.target.value }) }}/>
                <br/> <br/>
                <button className="btn btn-lg btn-primary btn-block" type="submit" onClick={this.validatelogin}>Sign in</button>
                <p className="mt-5 mb-3 text-muted">© 2017-2019</p>
            </form>
        </div>
    );
    }
};

export default Login;