import React from 'react';
import Navbar from '../Component/Navbar'
import StudentTable from './StudentTable';




class Student extends React.Component {

    constructor() {
        super();
        this.state = {
            id:'',
            studentinfo:[],
            contacts: [],
            
            contact: [{id:'',
                       f_name: 'rahul' ,
                        }],
            sendSMSInd:0,  
            SMSBody:"", 
            search:'',
            search_class:'',          
            
        }

        // preserve the initial state in a new object
    this.baseState = this.state
    }
    submit =()=> {
        
        fetch('http://localhost:8080/api/v1/Students', {
            method: 'post',
            body: JSON.stringify(
                
                this.state
            ),
            headers: {
                'Content-type': 'application/json'
            }
        }).then((response) => { response.json().then((res) => { console.log(res) } ) })
        

    }
    clearForm =(event)=>{
        event.preventDefault()
                this.setState({ SMSBody: '' })
                this.setState({ id: '' })
                this.setState({ f_name: '' })
                this.setState({ m_name: '' })
                this.setState({ l_name: '' })
                this.setState({ roll_number: '' })
                this.setState({ gender: ''})
                this.setState({ father_name: '' })
                this.setState({ mother_name: '' })
                this.setState({ dob:'' })
                this.setState({ classnumber: '' })
                this.setState({ address: '' })
                this.setState({ mobile: '' })
                this.setState({ adhar_number: '' })
    }

    deleteStudent =()=> {
        
        fetch('http://localhost:8080/api/v1/Students/'+this.state.id, {
            method: 'post',
           
        }).then((response) => { response.json().then((res) => { console.log(res) }) })

    }
    searchStudent =(event)=>{
        // this.setState(this.baseState)
        event.preventDefault()
            fetch(`http://localhost:8080/api/v1/Students/data/${this.state.f_name}/${this.state.l_name}`)
            .then(res => res.json())
            .then((data) => {
                this.setState({ contacts: data })
                
            })
            .catch(console.log)

            
    }
    sendSMS(){

        this.setState({sendSMSInd:1})
        fetch(`http://localhost:8080/api/v1/Students/SMS/${this.state.classnumber}/${this.state.SMSBody}`, {
            method: 'post',
           
        }).then((response) => { response.json().then((res) => { console.log(res) }) })
    }

    handlechangeAll =(event)=>  {
        this.setState({[event.target.name] :event.target.value})
    }

    populateForm=(data)=>{
        fetch('http://localhost:8080/api/v1/Students/'+data)
            .then(res => res.json())
            .then((data) => {
                this.setState({ id: data.id })
                this.setState({ f_name: data.f_name })
                this.setState({ m_name: data.m_name })
                this.setState({ l_name: data.l_name })
                this.setState({ roll_number: data.roll_number })
                this.setState({ gender: data.gender })
                this.setState({ father_name: data.father_name })
                this.setState({ mother_name: data.mother_name })
                this.setState({ dob: data.dob })
                this.setState({ classnumber: data.classnumber })
                this.setState({ address: data.address })
                this.setState({ mobile: data.mobile })
                this.setState({ adhar_number: data.adhar_number })
                
                
                    
            })
            .catch(console.log)
    }
    componentDidMount() {
        fetch('http://localhost:8080/api/v1/Students')
        // fetch(`http://localhost:8080/api/v1/Students/data/${this.state.f_name}/${this.state.l_name}`)
            .then(res => res.json())
            .then((data) => {
                this.setState({ contacts: data })
                
            })
            .catch(console.log)
    }  

   

    handleFormSubmit = (event)=> {
        event.preventDefault()
    }
    render() {

        console.log(this.state.contacts)
        let filtersearch = this.state.contacts.filter( (fcontact)=> {
            return fcontact.f_name.toLowerCase().includes(this.state.search.toLowerCase()) && fcontact.classnumber.includes(this.state.search_class)
          }
        )
        

        
        return (
            <div >

                
                    <form>
                    <div className="form-row">
                        <div className='col'>
                        <fieldset disabled>
                            <input type="text" className="form-control"   value = {this.state.id} placeholder="id (for Internal use)"  name = 'id' onChange={this.handlechangeAll} readonly></input>
                         </fieldset>  
                        </div>
                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.f_name} placeholder="First name" name='f_name' onChange={this.handlechangeAll}></input>
                        </div>
                        <div className="col">
                            <input type="text" className="form-control"  value = {this.state.m_name} placeholder="Middle name" onChange={(item) => { this.setState({ m_name: item.target.value }) }}></input>
                        </div>
                        <div className="col">
                            <input type="text" className="form-control" value = {this.state.l_name} placeholder="Last name" onChange={(item) => { this.setState({ l_name: item.target.value }) }}></input>
                        </div>
                    </div>
                    <br />

                    <div className="form-row">
                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.gender} placeholder="Gender" onChange={(item) => { this.setState({ gender: item.target.value }) }}></input>
                        </div>
                        <div className="col">
                            <input type="text" className="form-control" value = {this.state.classnumber} placeholder="Class" onChange={(item) => { this.setState({ classnumber: item.target.value }) }}></input>
                        </div>
                        <div className="col">
                            <input type="text" className="form-control" value = {this.state.roll_number} placeholder="Roll Number" onChange={(item) => { this.setState({ roll_number: item.target.value }) }}></input>
                        </div>

                        <div className="col">
                            <input type="text" className="form-control" value = {this.state.adhar_number} placeholder="Adhar Number" onChange={(item) => { this.setState({ adhar_number: item.target.value }) }}></input>
                        </div>
                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.dob} placeholder="Date of Birth" onChange={(item) => { this.setState({ dob: item.target.value }) }}></input>
                        </div>
                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.mobile} placeholder="Mobile Number" onChange={(item) => { this.setState({ mobile: item.target.value }) }}></input>
                        </div>
                    </div>
                    <br />
                    <div className='form-row'>


                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.father_name} placeholder="Father's name" onChange={(item) => { this.setState({ father_name: item.target.value }) }}></input>
                        </div>
                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.mother_name} placeholder="Mother's name" onChange={(item) => { this.setState({ mother_name: item.target.value }) }}></input>
                        </div>

                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.address} placeholder="Address" onChange={(item) => { this.setState({ address: item.target.value }) }}></input>
                        </div>
                    </div>
                    <br /><br />



                    <div >
                        <div className="row">
                            <div className="col-sm">
                                <button className="btn btn-success" onClick={(event) => { this.submit() }}>Add/Update Student</button>
                            </div>
                            <div className="col-sm">
                                <button className="btn btn-warning" onClick={(event) => { this.clearForm(event) }} >Clear Form</button>
                            </div>
                            <div className="col-sm">
                                <button className="btn btn-danger"  onClick={() => { this.deleteStudent() ;console.log(this.state.contact)}}>Delete Student</button>
                            </div>
                            
                            <div className="col-sm">
                                <button className="btn btn-outline-dark"  onClick={() => { this.sendSMS() }}>Send SMS</button>
                            </div>
                        </div>
                    </div>
                </form>
                <br />


                <div class="form-group">
                    
                    <textarea class="form-control" id="exampleFormControlTextarea1" value = {this.state.SMSBody} onChange={(item) => { this.setState({ SMSBody: item.target.value }) }} rows="3" placeholder="SMS Body : write the message in Any language. Use * in class box to send message to all students else use the class number to send the message to a perticular class." ></textarea>
                </div>

                <br />
                <div className="form-row">
                <div className='col'>
                            <input type="text" className="form-control" value = {this.state.search} placeholder="Search First Name" onChange={(item) => { this.setState({ search: item.target.value }) }}></input>
                        </div>
                        <div className='col'>
                            <input type="text" className="form-control" value = {this.state.search_class} placeholder="Search Class" onChange={(item) => { this.setState({ search_class: item.target.value }) }}></input>
                        </div>        
                </div>        
                <br /><br />
                <StudentTable filtersearch ={filtersearch} populateForm = {this.populateForm}></StudentTable>

                

            </div>

        );
    }
}

export default Student;