import React from 'react';
import Navbar from '../Component/Navbar'
import Footer from '../Component/Footer'
import Student from '../Component/Student'
import Login from '../Component/Login';


class StudentAdmin extends React.Component {

    constructor(){
        super()
        const token1 = localStorage.getItem('token')
        
        console.log(localStorage)
        let isLoggedin = true
        if (token1== null) {
            isLoggedin = false
        }
        this.state = {isLoggedin}
        
        
    }

    validatelogin =() =>{
     this.setState({ isLoggedin: true })

    }
    render() {

        return this.state.isLoggedin ? 
        (
            <div className="container-sm">
                <Navbar />
                <button className="btn btn-info  btn-cm float-right" onClick={()=> {localStorage.removeItem('token');this.setState({ isLoggedin: false })} }>Log out</button>
                <br/><br/>
                <Student/>               
                <Footer />
            </div>
        ) :
        (
            <div className="container-sm">
                <Navbar />
                
                <Login validatelogin = {this.validatelogin}/>            
                <Footer />
            </div>
        )

        
    }
}

export default StudentAdmin;